d var(--color-lightgray);\n}\n\nheader {\n  position: relative;\n  text-align: center;\n}\n\n.logo {\n  text-decoration: none;\n  font-size: var(--text-xxxxl);\n  display: inline-flex;\n  align-items: center;\n  gap: 0;\n  font-family: \"Kaushan Script\", cursive;\n  padding: var(--space-xs) 0;\n}\n\n.logo-word-todo {\n  color: var(--color-accent1);\n}\n.logo-word-list {\n  position: relative;\n  color: var(--color-darkgray);\n}\n\n.logo-image {\n  width: 30px;\n}\n\n.button-nav-open {\n  position: absolute;\n  inset: var(--space-sm) 0 0 var(--space-sm);\n  cursor: pointer;\n  height: 50px;\n  width: 50px;\n  margin: 0;\n  padding: 0;\n  border: none;\n  border-radius: var(--border-radius);\n  background-color: var(--color-lightgray);\n  transition: opacity 250ms ease;\n}\n\n.button-nav-open:focus,\n.button-nav-open:hover {\n  opacity: 0.75;\n}\n\n.hamburger {\n  width: 50%;\n  position: relative;\n}\n\n.hamburger,\n.hamburger::before,\n.hamburger::after {\n  display: block;\n  margin: 0 auto;\n  height: 3px;\n  background-color: var(--color-darkgray);\n}\n\n.hamburger::before,\n.hamburger::after {\n  content: \"\";\n  width: 100%;\n}\n\n.hamburger::before {\n  transform: translateY(-7px);\n}\n\n.hamburger::after {\n  transform: translateY(4px);\n}\n\n.nav {\n  grid-area: nav;\n  padding: var(--space-md) var(--space-xs);\n  position: absolute;\n  inset: 0;\n  background-color: var(--color-eggshell);\n  width: 90%;\n  margin: var(--space-md) auto;\n  border: 1px solid var(--color-gray);\n  border-radius: var(--border-radius);\n  min-height: max-content;\n  display: none;\n  filter: var(--drop-shadow-md);\n  text-align: center;\n  z-index: 1;\n}\n\n.nav.open {\n  display: block;\n}\n\n.nav__title {\n  font-family: \"Barlow Semi Condensed\", sans-serif;\n  margin-bottom: var(--space-md);\n}\n\n.nav__list {\n  display: grid;\n  grid-auto-flow: row;\n  gap: var(--space-sm);\n  grid-template-columns: minmax(100px, 250px);\n  justify-content: center;\n  text-decoration: none;\n  list-style: none;\n  padding: 0;\n  margin-bottom: var(--space-xxl);\n}\n\n.nav__item {\n  font-family: \"Montserrat\", sans-serif;\n}\n\n.nav__item:hover {\n  opacity: 65%;\n}\n\n.nav__link-timeframe {\n  display: inline-block;\n  text-decoration: none;\n  font-weight: 500;\n  color: var(--color-accent2-alt);\n  background-color: var(--color-lightgray);\n  padding: 0.5em 0;\n  width: 100%;\n  border-radius: var(--border-radius);\n}\n\n.nav__link-project {\n  color: var(--color-accent1-alt);\n  font-weight: 500;\n  text-decoration: none;\n}\n\n.nav__link-timeframe:focus,\n.nav__link-timeframe:hover,\n.nav__link-project:focus,\n.nav__link-project:hover {\n  text-decoration: underline;\n}\n\n.nav__link-settings {\n  display: inline-block;\n  width: min(250px, 100%);\n  padding: var(--space-md) 0;\n  border-top: 1px solid var(--color-lightgray);\n\n  text-decoration: none;\n  color: var(--color-darkgray);\n}\n\n.nav__button-close {\n  position: absolute;\n  right: var(--space-md);\n  top: var(--space-md);\n  width: 44px;\n  height: 44px;\n  border: 0;\n  background: none;\n}\n.nav__button-close:focus,\n.nav__button-close:hover {\n  opacity: 0.65;\n  cursor: pointer;\n}\n\n.nav__button-close::before,\n.nav__button-close::after {\n  position: absolute;\n  left: 50%;\n  bottom: 0;\n  content: \" \";\n  height: 44px;\n  width: 2px;\n  background: var(--color-darkgray);\n}\n\n.nav__button-close::before {\n  transform: rotate(45deg);\n}\n.nav__button-close::after {\n  transform: rotate(-45deg);\n}\n\n.backdrop {\n  pointer-events: none;\n  position: fixed;\n  inset: 0;\n  opacity: 0;\n}\n\n.modal {\n  background-color: var(--color-eggshell);\n  width: min(90%, 450px);\n  position: fixed;\n  top: 50%;\n  left: 50%;\n  transform: translate(-50%, 0%);\n  pointer-events: none;\n  opacity: 0;\n  padding: var(--space-lg);\n  border: 1px solid var(--color-gray);\n  border-radius: var(--border-radius);\n\n  transition:\n    transform 175ms ease-out,\n    opacity 200ms ease;\n}\n\n.modal__button-close {\n  position: absolute;\n  right: var(--space-md);\n  top: var(--space-md);\n  width: 44px;\n  height: 44px;\n  border: 0;\n  background: none;\n}\n\n.modal__button-close:hover {\n  opacity: 0.65;\n  cursor: pointer;\n}\n\n.modal__button-close::before,\n.modal__button-close::after {\n  position: absolute;\n  left: 50%;\n  bottom: 0;\n  content: \" \";\n  height: 44px;\n  width: 2px;\n  background: var(--color-darkgray);\n}\n\n.modal__button-close::before {\n  transform: rotate(45deg);\n}\n.modal__button-close::after {\n  transform: rotate(-45deg);\n}\n\n.modal.show {\n  opacity: 1;\n  pointer-events: all;\n  transform: translate(-50%, -50%);\n  z-index: 2;\n}\n\n.modal__title,\n.modal__span-required {\n  padding: 0;\n  line-height: 0;\n}\n\n.modal__title {\n  font-family: \"Barlow Semi Condensed\", sans-serif;\n  padding-block: 0.5em;\n}\n\n.form {\n  display: grid;\n  gap: var(--space-lg);\n}\n\nspan[aria-label=\"required\"] {\n  color: red;\n}\n\n.form__group {\n  display: grid;\n  gap: var(--space-xxs);\n  grid-template-columns: minmax(100px, 1fr);\n}\n\n.form__label {\n  font-weight: 500;\n  color: var(--color-darkgray);\n  font-family: \"Barlow Semi Condensed\", sans-serif;\n  font-size: var(--text-md);\n}\n\n.form__input {\n  color: var(--color-charcoal);\n  border: 1px solid var(--color-gray);\n  border-radius: 5px;\n  padding: var(--space-xxs) var(--space-sm);\n}\n\n.form__input:focus {\n  outline: 2px solid lime;\n}\n\n.form__input::placeholder {\n  color: var(--color-gray);\n}\n\n.form__buttons {\n  text-align: right;\n}\n\n.form__button {\n  font-weight: 600;\n  border: 0;\n  border-radius: 3px;\n  padding: 0.375em 1em;\n  color: white;\n  cursor: pointer;\n}\n\n.form__button:focus,\n.form__button:hover {\n  opacity: 0.8;\n}\n\n.form__button--save {\n  background: #60a5fa;\n  margin-right: var(--space-xs);\n}\n\n.form__button--cancel {\n  background: var(--color-gray);\n}\n\n.form__button--cancel {\n}\n\n.backdrop.show {\n  back